.container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 20px;
    height: 90px;
    border: 1px solid #808080;
    border-radius: 10px;
}

.link {
    font-weight: 600;
    font-size: 16px;

    color: #464646;
    text-decoration: none;
}

.info {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.userData {
    font-size: 12px;
    color: #a8a8a8;
}

.score {
    padding: 6px 12px;
    background-color: #8f8f8f;
    color: white;
    border-radius: 20px;
}

.lowScore {
    background-color: #8f8f8f;
    color: white;
}

.midScore {
    background-color: #ffc377;
    color: #ff8d00;
}


.highScore {
    background-color: #d9ff89;
    color: #8bbb0b;
}

.externalLink {
    color: #ff8d00;
    cursor: pointer;
}